
import React, { useState } from 'react';
import { useAudio } from '@/contexts/AudioContext';
import { Button } from '@/components/ui/button';
import { Card, CardContent } from '@/components/ui/card';
import { 
  Play, 
  Pause, 
  SkipBack, 
  SkipForward, 
  Volume2, 
  VolumeX, 
  Maximize2, 
  Minimize2,
  Music,
  Waves
} from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';
import { Slider } from '@/components/ui/slider';

export function FloatingAudioPlayer() {
  const { 
    isPlaying, 
    togglePlay, 
    currentTrackIndex,
    volume, 
    setVolume,
    isMuted, 
    muteAudio, 
    unmuteAudio,
    playlist,
    nextTrack,
    prevTrack,
    reverbEnabled,
    toggleReverb,
    reverbAmount,
    setReverbAmount
  } = useAudio();
  
  const [isExpanded, setIsExpanded] = useState(false);
  const [isVisible, setIsVisible] = useState(true);

  // Get current track safely
  const currentTrack = playlist[currentTrackIndex];

  // Don't show if no track is loaded or playing
  if (!currentTrack && !isPlaying) {
    return null;
  }

  const handleVolumeChange = (newValue: number[]) => {
    setVolume(newValue[0]);
  };

  const handleReverbAmountChange = (newValue: number[]) => {
    setReverbAmount(newValue[0]);
  };

  return (
    <AnimatePresence>
      {isVisible && (
        <motion.div
          initial={{ opacity: 0, y: 100 }}
          animate={{ opacity: 1, y: 0 }}
          exit={{ opacity: 0, y: 100 }}
          className="fixed bottom-6 right-6 z-50"
        >
          <Card className="bg-[#1A1F2C]/95 backdrop-blur-md border-gold/20 shadow-2xl">
            <CardContent className="p-0">
              {isExpanded ? (
                <motion.div
                  initial={{ height: 60 }}
                  animate={{ height: 'auto' }}
                  className="w-80"
                >
                  {/* Expanded Player */}
                  <div className="p-4 space-y-4">
                    {/* Header */}
                    <div className="flex items-center justify-between">
                      <div className="flex items-center gap-2">
                        <Music className="w-5 h-5 text-gold" />
                        <span className="text-sm font-medium text-white">Sacred Audio Player</span>
                      </div>
                      <Button
                        variant="ghost"
                        size="sm"
                        onClick={() => setIsExpanded(false)}
                        className="text-white/70 hover:text-gold h-6 w-6 p-0"
                      >
                        <Minimize2 className="w-4 h-4" />
                      </Button>
                    </div>

                    {/* Track Info */}
                    {currentTrack && (
                      <div className="text-center">
                        <h4 className="font-medium text-white text-sm mb-1">
                          {currentTrack.name}
                        </h4>
                        <p className="text-xs text-white/60">
                          {currentTrack.description || 'Sacred Orthodox Chant'}
                        </p>
                      </div>
                    )}

                    {/* Main Controls */}
                    <div className="flex items-center justify-center gap-2">
                      <Button
                        variant="ghost"
                        size="sm"
                        onClick={prevTrack}
                        className="text-white/70 hover:text-gold h-8 w-8 p-0"
                      >
                        <SkipBack className="w-4 h-4" />
                      </Button>
                      
                      <Button
                        variant="ghost"
                        size="sm"
                        onClick={togglePlay}
                        className="text-gold hover:text-gold/80 bg-gold/10 h-10 w-10 p-0 rounded-full"
                      >
                        {isPlaying ? (
                          <Pause className="w-5 h-5" />
                        ) : (
                          <Play className="w-5 h-5 ml-0.5" />
                        )}
                      </Button>
                      
                      <Button
                        variant="ghost"
                        size="sm"
                        onClick={nextTrack}
                        className="text-white/70 hover:text-gold h-8 w-8 p-0"
                      >
                        <SkipForward className="w-4 h-4" />
                      </Button>
                    </div>

                    {/* Volume Control */}
                    <div className="flex items-center gap-2 px-2">
                      <Button
                        variant="ghost"
                        size="sm"
                        onClick={isMuted ? unmuteAudio : muteAudio}
                        className="text-white/70 hover:text-gold h-6 w-6 p-0"
                      >
                        {isMuted ? (
                          <VolumeX className="w-4 h-4" />
                        ) : (
                          <Volume2 className="w-4 h-4" />
                        )}
                      </Button>
                      <div className="flex-1">
                        <Slider
                          value={[volume]}
                          max={100}
                          step={1}
                          onValueChange={handleVolumeChange}
                          className="cursor-pointer"
                        />
                      </div>
                    </div>

                    {/* Cathedral Reverb Controls */}
                    <div className="border-t border-gold/20 pt-3">
                      <div className="flex items-center justify-between mb-2">
                        <span className="text-xs font-medium text-gold/80">Cathedral Reverb</span>
                        <Button
                          variant={reverbEnabled ? "default" : "ghost"}
                          size="sm"
                          onClick={toggleReverb}
                          className={reverbEnabled 
                            ? "bg-gold/30 text-gold hover:bg-gold/40 h-6 text-xs" 
                            : "text-white/70 hover:text-gold h-6 text-xs"
                          }
                        >
                          <Waves className="w-3 h-3 mr-1" />
                          {reverbEnabled ? 'ON' : 'OFF'}
                        </Button>
                      </div>
                      
                      {reverbEnabled && (
                        <div className="flex items-center gap-2">
                          <span className="text-xs text-white/60">Amount:</span>
                          <Slider
                            value={[reverbAmount]}
                            max={100}
                            step={1}
                            onValueChange={handleReverbAmountChange}
                            className="cursor-pointer flex-1"
                          />
                          <span className="text-xs text-gold/70 min-w-8">{reverbAmount}%</span>
                        </div>
                      )}
                    </div>

                    {/* Playlist Preview */}
                    <div className="border-t border-gold/20 pt-3">
                      <div className="text-xs font-medium text-gold/80 mb-2">Sacred Chants Collection</div>
                      <div className="space-y-1 max-h-32 overflow-y-auto">
                        {playlist.slice(0, 3).map((track, index) => (
                          <div 
                            key={index}
                            className={`text-xs p-2 rounded flex items-center gap-2 ${
                              currentTrackIndex === index 
                                ? 'bg-gold/10 text-gold' 
                                : 'text-white/60 hover:text-white/80'
                            }`}
                          >
                            <span>{track.icon}</span>
                            <span className="truncate">{track.name}</span>
                          </div>
                        ))}
                        {playlist.length > 3 && (
                          <div className="text-xs text-white/40 text-center">
                            +{playlist.length - 3} more chants
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                </motion.div>
              ) : (
                /* Minimized Player */
                <div className="flex items-center gap-3 p-3">
                  <Button
                    variant="ghost"
                    size="sm"
                    onClick={togglePlay}
                    className="text-gold hover:text-gold/80 bg-gold/10 h-9 w-9 p-0 rounded-full"
                  >
                    {isPlaying ? (
                      <Pause className="w-4 h-4" />
                    ) : (
                      <Play className="w-4 h-4 ml-0.5" />
                    )}
                  </Button>
                  
                  {currentTrack && (
                    <div className="min-w-0 flex-1">
                      <p className="text-xs font-medium text-white truncate">
                        {currentTrack.name}
                      </p>
                      <p className="text-xs text-white/60 truncate">
                        {currentTrack.description || 'Sacred Orthodox Chant'}
                      </p>
                    </div>
                  )}
                  
                  <Button
                    variant="ghost"
                    size="sm"
                    onClick={() => setIsExpanded(true)}
                    className="text-white/70 hover:text-gold h-6 w-6 p-0"
                  >
                    <Maximize2 className="w-4 h-4" />
                  </Button>
                </div>
              )}
            </CardContent>
          </Card>
        </motion.div>
      )}
    </AnimatePresence>
  );
}
